export function parse(ntp: uint64) {
  const sec = ntp >> 32n
  const fracPart = ntp & 0xFFFFFFFFn
  const usec = (fracPart * 1000000n) / 0xFFFFFFFFn
  return (sec * 1000000n) + usec
}
